    "id": "recXHq9OGXDyp0Nwi",
    "name": "âš¡ Excessive Boiler Operation",
    "status": "On Hold",
    "assigned": "Matt Sharpe",
    "priority": "Maintenance",
    "description": "- Boilers, SHHWPs and PHHWPs are being enabled to run for extended periods of time due to faulty field controllers representing static 100% hot water valve positions.\n- When 1-2 controllers fall into fault the number of valve above 40% is always true and the average valve position is between 11 - 22%, preventing the heating call from being disabled.",
    "solution": "- Adjust the heating call logic to disable the heating call when the average valve position is under 25% (will be false if only 2 hot water valves are 100% and the rest are closed)\n- Ensure the heating call is disabled when the number of valves above 40% is less than 3.\n",
    "mailto": "mailto:howie.mann@cim.io?cc=engineering@cim.io&subject=Chirnside%20Park%20Shopping%20Centre Action Required: %E2%9A%A1%20Excessive%20Boiler%20Operation&body=Issue: %0D%0A-%20Boilers%2C%20SHHWPs%20and%20PHHWPs%20are%20being%20enabled%20to%20run%20for%20extended%20periods%20of%20time%20due%20to%20faulty%20field%20controllers%20representing%20static%20100%25%20hot%20water%20valve%20positions.%0A-%20When%201-2%20controllers%20fall%20into%20fault%20the%20number%20of%20valve%20above%2040%25%20is%20always%20true%20and%20the%20average%20valve%20position%20is%20between%2011%20-%2022%25%2C%20preventing%20the%20heating%20call%20from%20being%20disabled.%0D%0A %0D%0AEquipment: %0D%0AHWS_Common%0D%0A %0D%0A Action Required: %0D%0A-%20Adjust%20the%20heating%20call%20logic%20to%20disable%20the%20heating%20call%20when%20the%20average%20valve%20position%20is%20under%2025%25%20%28will%20be%20false%20if%20only%202%20hot%20water%20valves%20are%20100%25%20and%20the%20rest%20are%20closed%29%0A-%20Ensure%20the%20heating%20call%20is%20disabled%20when%20the%20number%20of%20valves%20above%2040%25%20is%20less%20than%203.%0A%0D%0A %0D%0ATap Link Below When Actioned: %0D%0Ahttps%3A%2F%2Fairtable.com%2FshrlGpoqubitFNdl4%3Fprefill_Issue%3DrecXHq9OGXDyp0Nwi%26prefill_Contact%3DMatt%2520Sharpe%0D%0A %0D%0A %0D%0A %0D%0A %0D%0Arecord_id: recXHq9OGXDyp0Nwi",
